.contact-detail {
	margin: 40px 0 0;
	background: $white;
	padding: 40px;
	text-align: center;
	height: calc(100% - 40px);
	.fa {
		display: inline-flex;
		align-items: center;
		align-content: center;
		justify-content: center;
		@include fs(48rem);
		color: $color-primary;
		width: 86px;
		height: 86px;
		@include bor-rad(100%);
		background: color-rgba($color-default,0.5);
		margin-bottom: 15px; }
	.title {
		margin: 10px 0;
		@include fs(18rem);
		line-height: 1;
		font-weight: 700;
		color: $color-primary; } }

.contact-page {
	margin: 40px 0;
	background: $white;
	padding: 40px;
	.contact-form {
		.module-title {
			@include fs(14rem);
			font-weight: 500;
			text-align: center;
			margin-bottom: 10px; }
		.form-wrap {
			max-width: 700px;
			margin: 0 auto; } }
	.btn-wrap {
		text-align: center;
		input[type="submit"] {
			width: auto;
			@include bor-rad(5px);
			background: $color-primary;
			color: $white;
			@include fs(14rem); } } }
