.category-block {
	background: $white;
	.categories {
		ul {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			li {
				padding: 15px 15px;
				a {
					@include fs(13rem);
					text-transform: capitalize; }
				&.active {
					a {
						color: $color-default; } } } } } }

.picture-page {
	margin: 40px 0;
	.card-columns {
		.card {
			margin-bottom: 20px;
			border: none;
			box-shadow: none; } } }

.video-page {
	margin: 40px 0;
	.video-block {
		background: $white;
		@include flexbox;
		.video-frame {
			width: 70%; }
		.video-list {
			width: 30%;
			height: 440px;
			overflow: auto;
			&::-webkit-scrollbar-track {
				background-color: darken($white, 20%);
				@include bor-rad(100px); }
			&::-webkit-scrollbar {
				width: 6px;
				background-color: #fff;
				@include bor-rad(100px); }
			&::-webkit-scrollbar-thumb {
				background-color: $color-primary;
				@include bor-rad(100px); }
			.video-item {
				@include flexbox;
				padding: 10px 15px;
				@include pos(rel,null);
				&:before {
					content: '';
					display: block;
					@include pos(abs, 0 null 0 0);
					width: 5px;
					background: $color-default;
					opacity: 0; }
				&.active {
					background: color-rgba($color-default, 0.05);
					&:before {
						opacity: 1; }
					.info {
						.name {
							color: $color-default; } } }
				.img {
					width: 120px; }
				.info {
					flex: 1;
					padding: 5px 10px;
					.name {
						@include fs(14rem);
						line-height: 1.3;
						font-weight: 500; }
					time {
						@include fs(11rem);
						opacity: 0.5; } } } } } }
